import Vue from 'vue';
import { identify } from 'ambients-utils';
var checkedMap = {};
export default Vue.extend({
    props: {
        name: String,
        checked: Boolean
    },
    data: function () {
        return {
            checkedMap: checkedMap[this.name] || (checkedMap[this.name] = { id: '' }),
            isChecked: this.checked
        };
    },
    methods: {
        check: function () {
            this.checkedMap.id = identify(this);
        }
    },
    watch: {
        'checkedMap.id': function (id) {
            this.isChecked = id === identify(this);
        },
        isChecked: function (val) {
            this.$emit(val ? 'check' : 'uncheck');
        }
    }
});
