import Vue from 'vue';
import rinss, { rss } from 'rinss';
import theme from './theme';
var css = rinss.create({
    hideFirstChild: {
        svg: {
            ':first-child:not(:last-child)': {
                display: 'none'
            }
        }
    },
    hideLastChild: {
        svg: {
            ':last-child:not(:first-child)': {
                display: 'none'
            }
        }
    }
});
Vue.component('icon', {
    template: "\n        <div :class=\"computedClass\" :style=\"computedStyle\"><slot/></div>\n    ",
    props: {
        size: {
            type: String,
            default: '20px'
        },
        active: Boolean
    },
    computed: {
        computedClass: function () {
            return this.active ? css.hideFirstChild : css.hideLastChild;
        },
        computedStyle: function () {
            return rss({
                width: this.size,
                height: this.size,
                color: this.active ? theme.primary : theme.textPrimary
            });
        }
    }
});
