import "normalize.css";
import "babel-polyfill";
import Vue from "vue";
import rinss from "rinss";
import Modal from 'ambients-modal';
import { Sketch } from 'vue-color';
import VueResize from 'vue-resize';
Vue.use(VueResize);
import VueMaterial from "vue-material";
import "vue-material/dist/vue-material.min.css";
import 'vue-material/dist/theme/default.css';
Vue.use(VueMaterial);
import AsyncComputed from 'vue-async-computed';
Vue.use(AsyncComputed);
Vue.mixin({
    methods: {
        $emitState: function (name, value) {
            this.$emit(name, value);
            (this.$stateValue || (this.$stateValue = {}))[name] = value;
            (this.$stateEmitted || (this.$stateEmitted = {}))[name] = true;
        },
        $onState: function (name, cb) {
            if ((this.$stateEmitted || (this.$stateEmitted = {}))[name])
                cb.call(this, (this.$stateValue || (this.$stateValue = {}))[name]);
            this.$on(name, cb);
        }
    }
});
import VuePerfectScrollbar from 'vue-perfect-scrollbar';
import VueTouch from 'vue-touch';
Vue.use(VueTouch);
import './panels';
import './propertiesPanel';
import './positionPanel';
import './toolbar';
import './typographyPanel';
import './row';
import './whiteBoard';
import './transformPanel';
import './borderPanel';
import './outline';
rinss.config({ duration: 250 });
var container = document.createElement('div');
document.body.appendChild(container);
rinss.inline(document.body, {
    overflow: 'hidden',
    fontFamily: '"Arial", "sans-serif"'
});
var css = rinss.create({
    stage: {
        width: '100vw',
        height: '100vh',
        top: 0,
        left: 0
    },
    test: {
        width: 100,
        height: 100,
        background: 'red',
        floatTop: 10
    },
    colorPickerModal: {
        zIndex: 9999,
        boxShadow: '0 19px 38px rgba(0,0,0,0.10), 0 15px 12px rgba(0,0,0,0.05)'
    },
    sketchPicker: {
        userSelect: 'none'
    }
});
var colorPicker = {
    show: false,
    color: {
        hex: '#ff0000'
    },
    left: 0,
    top: 0
};
var textColorPicker = {
    show: false,
    color: {
        hex: '#000000'
    },
    left: 0,
    top: 0
};
new Vue({
    el: container,
    components: {
        'modal': Modal,
        'sketch-picker': Sketch,
        'perfect-scrollbar': VuePerfectScrollbar
    },
    template: "\n        <div class=\"" + css.stage + "\">\n            <row stretch stretchy>\n                <cell shrink><toolbar @showColorPicker=\"showColorPicker\" :colorPicked=\"colorPicker.color.hex\"/></cell>\n                <cell shrink><outline/></cell>\n                <cell><white-board/></cell>\n                <cell shrink><panels>\n                    <properties-panel expanded/>\n                    <position-panel expanded/>\n                    <typography-panel\n                    @showColorPicker=\"showTextColorPicker\"\n                    :colorPicked=\"textColorPicker.color.hex\"\n                    expanded/>\n                    <panel title=\"Backgrounds\"/>\n                    <panel title=\"Effects\"/>\n                    <transform-panel/>\n                    <border-panel/>\n                </panels></cell>\n            </row>\n            <modal class=\"" + css.colorPickerModal + "\"\n             v-if=\"colorPicker.show\"\n             :left=\"colorPicker.left\"\n             :top=\"colorPicker.top\"\n             @close=\"colorPicker.show=false\">\n                <sketch-picker class=\"" + css.sketchPicker + "\" v-model=\"colorPicker.color\"/>\n            </modal>\n            <modal class=\"" + css.colorPickerModal + "\"\n             v-if=\"textColorPicker.show\"\n             :left=\"textColorPicker.left\"\n             :top=\"textColorPicker.top\"\n             @close=\"textColorPicker.show=false\">\n                <sketch-picker class=\"" + css.sketchPicker + "\" v-model=\"textColorPicker.color\"/>\n            </modal>\n        </div>\n    ",
    data: function () {
        return {
            colorPicker: colorPicker,
            textColorPicker: textColorPicker
        };
    },
    methods: {
        showColorPicker: function (e) {
            this.colorPicker.left = e.clientX;
            this.colorPicker.top = e.clientY;
            this.colorPicker.show = true;
        },
        showTextColorPicker: function (e) {
            this.textColorPicker.left = e.clientX;
            this.textColorPicker.top = e.clientY;
            this.textColorPicker.show = true;
        },
        onPan: function (e) {
            console.log(e);
        }
    }
});
