import Vue from 'vue';
import rinss, { rss } from 'rinss';
var css = rinss.create({
    shrink: {
        whiteSpace: 'nowrap',
        width: 1
    },
    grow: {
        whiteSpace: 'nowrap',
        '>*': {
            width: '100%',
        }
    },
    auto: {
        whiteSpace: 'nowrap'
    }
});
Vue.component('cell', {
    template: "\n        <td align=\"center\" :class=\"computedClass\"><slot/></td>\n    ",
    inject: {
        stretch: { default: false }
    },
    props: {
        shrink: Boolean
    },
    computed: {
        computedClass: function () {
            return this.shrink ? css.shrink : (this.stretch ? css.grow : css.auto);
        }
    }
});
Vue.component('gap', {
    template: "\n        <td align=\"center\" class=\"" + css.shrink + "\"><div :style=\"computedStyle\"/></td>\n    ",
    props: {
        size: {
            type: String,
            default: '10px'
        }
    },
    computed: {
        computedStyle: function () {
            return rss({ width: this.size, height: '100%' });
        }
    }
});
Vue.component('row', {
    template: "\n        <table :style=\"tableStyle\"><tr><slot/></tr></table>\n    ",
    props: {
        stretch: Boolean,
        stretchy: Boolean
    },
    provide: function () {
        return {
            stretch: this.stretch
        };
    },
    computed: {
        tableStyle: function () {
            return rss({
                width: this.stretch ? '100%' : 'auto',
                height: this.stretchy ? '100%' : 'auto',
                borderCollapse: 'collapse'
            });
        }
    }
});
