import Vue from "vue";
import rinss, { rss } from "rinss";
var css = rinss.create({
    label: {
        fontSize: 13,
        important: true
    },
    wrapper: {
        width: '100%'
    },
    field: {
        marginBottom: 10,
        important: true
    },
    select: {
        input: {
            fontSize: 13,
            important: true
        }
    }
});
Vue.component('material-select', {
    template: "\n        <div class=\"" + css.wrapper + "\">\n            <md-field class=\"" + css.field + "\">\n                <label class=\"" + css.label + "\">{{placeholder}}</label>\n                <md-select class=\"" + css.select + "\" v-model=\"selected\">\n                    <md-option v-for=\"o of options\" :value=\"o\" :key=\"o\">{{o}}</md-option>\n                </md-select>\n                <select style=\"" + rss({ fillParent: true, opacity: 0, cursor: 'pointer' }) + "\" v-model=\"selected\">\n                    <option v-for=\"o of options\" :value=\"o\">{{o}}</option>\n                </select>\n            </md-field>\n        </div>\n    ",
    props: {
        placeholder: String,
        options: Array,
    },
    data: function () {
        return {
            selected: ''
        };
    }
});
