import Vue from "vue";
import rinss, { rss } from "rinss";
var css = rinss.create({
    label: {
        fontSize: 13,
        important: true
    },
    wrapper: {
        width: '100%'
    },
    field: {
        marginBottom: 10,
        important: true
    },
    input: {
        fontSize: 13,
        important: true
    }
});
Vue.component('material-input', {
    template: "\n        <div class=\"" + css.wrapper + "\" :style=\"computedStyle\">\n            <md-field class=\"" + css.field + "\">\n                <label class=\"" + css.label + "\">{{placeholder}}</label>\n                <md-input class=\"" + css.input + "\"\n                 :value=\"value\"\n                 @change.native=\"$emit('input', $event.target.value)\"/>\n            </md-field>\n        </div>\n    ",
    props: {
        placeholder: String,
        value: String
    },
    data: function () {
        return {
            width: ''
        };
    },
    mounted: function () {
        this.width = this.$el.clientWidth + 'px';
    },
    computed: {
        computedStyle: function () {
            return rss({ width: this.width });
        }
    }
});
