import Vue from 'vue';
import rinss, { rss } from 'rinss';
import theme from './theme';
var css = rinss.create({
    colorPicker: {
        borderRadius: 3,
        border: '1px solid ' + theme.textPrimary,
        position: 'relative',
        cursor: 'pointer',
        '>div': {
            width: '75%',
            height: '75%',
            centerX: true,
            centerY: true,
            border: '1px solid ' + theme.textPrimary
        }
    }
});
Vue.component('color-picker', {
    template: "\n        <div class=\"" + css.colorPicker + "\" :style=\"outerStyle\">\n            <div :style=\"innerStyle\"/>\n        </div>\n    ",
    props: {
        color: String,
        size: {
            type: String,
            default: '20px'
        }
    },
    computed: {
        outerStyle: function () {
            return rss({ width: this.size, height: this.size });
        },
        innerStyle: function () {
            return rss({ background: this.color });
        }
    }
});
